.graph {
    position: relative;
    width: 100%;
    height: 187.2px;
    margin-top: 13px;
}

.graph__gridbase {
    position: absolute;
    bottom: 0;
    width: 100%;
    background: radial-gradient(
        48.48% 23.87% at 39.69% 80.76%,
        rgba(27, 25, 61, 0.902542) 0%,
        rgba(35, 33, 76, 0.0001) 100%
    );
    z-index: 1;
}

.graph__path-0 {
    position: absolute;
    bottom: 0;
    z-index: 2;
}
.graph__path-1 {
    position: absolute;
    bottom: 0;
    z-index: 3;
}

.graph__path-2 {
    position: absolute;
    bottom: 0;
    z-index: 4;
}

.graph__path-3 {
    position: absolute;
    bottom: 0;
    z-index: 5;
}

.portfolio__bottom-listtime {
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 14px 16.7px 26px 16.7px;
}

.portfolio__bottom-listtime_item {
    font-family: 'HelveticaNeue';
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 0.428571px;

    color: #59588d;
}

@media screen and (max-width: 650px) {
    .portfolio__bottom {
        display: none;
    }

    .portfolio {
        width: 328px;
        height: 172px;
    }

    .portfolio__head {
        margin-left: 20px;
        flex-wrap: wrap;
    }
    .portfolio__head-title {
        margin-bottom: 1rem;
    }

    .portfolio__head-timelist {
        font-size: 8px;
    }

    .graph {
        width: 100%;
        height: 60px;
        margin-top: 13px;
        margin-bottom: 2rem;
    }

    .graph__path img,
    .graph__gridbase img {
        width: 100%;
    }
}

