.btn__current {
    width: 99px;
    height: 41px;
    background: #000000;
    border-radius: 12px;
    font-family: 'Brinnan';
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;

    color: #ffffff;
    cursor: pointer;
    border: 2px solid #33ace0;
}

@-webkit-keyframes show__overlay {
    from {
        top: -100%;
        opacity: 0;
    }
    to {
        top: 0;
        opacity: 1;
    }
}

@keyframes show__overlay {
    from {
        top: -100%;
        opacity: 0;
    }
    to {
        top: 0;
        opacity: 1;
    }
}

@media screen and (max-width: 650px) {
    #network__modal {
        width: 100%;
    }

    .network {
        width: 80%;
    }
    .network__list-item {
        width: 100%;
        padding: 8px;
    }

    .network__list {
        width: 100%;
    }
}

.pagination {
    margin-top: 2rem;
    display: inline-block;
}

.pagination a {
    color: #ffffff;
    float: left;
    padding: 8px 16px;
    text-decoration: none;
    transition: background-color 0.3s;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.pagination a:hover:not(.active) {
    background-color: #ddd;
    color: #000000;
}

@media screen and (max-width: 650px) {
    .pagination {
        padding: 0 3rem;
    }
}

