    display: flex;
    /* flex-wrap: wrap; */
    justify-content: space-between;
    align-items: center;
    margin-right: 50px;
    padding: 0 1rem;
}

.portfolio-funding__container {
    width: 100%;
    display: flex;
    margin-top: 33px;
    align-items: center;
}

.btn__fund-vaults_mb {
    display: none;
}

.assets__content-mb {
    display: none;
}

.assets__container {
    width: 1002px;
    background: #0e0e0e;
    border: 2px solid #1c1c1c;
    border-radius: 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 28px 0;
    margin-bottom: 3rem;
    flex: 1 1;
}

.assets__head {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 33px;
    margin-left: 40px;
}

.assets__head-title {
    font-family: 'Brinnan';
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: #ffffff;
}

.assets__head-title_sub {
    font-family: 'Brinnan';
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #ffffff;
    margin: 0 5px;
}

.assets__head-desc {
    font-family: 'Brinnan';
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #77838f;
}

.assets__content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.assets__content-list {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 40px;
    font-family: 'Brinnan';
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #77838f;
}

.assets__content-item_balance {
    padding-left: 1.5rem;
}

.assets__content-item_interest {
    padding-left: 1.5rem;
}

.wrapper__assets-notify {
    width: 100%;
    display: flex;
    align-items: flex-start;
    margin-top: 33px;
}

.notify__container {
    width: 351px;
    height: auto;
    background: #0e0e0e;
    border: 2px solid #1c1c1c;
    border-radius: 24px;
    margin-left: 31px;
    margin-right: 15px;
}

.notify__title {
    font-family: 'Brinnan';
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    margin: 19px 0 27px 30px;

    color: #ffffff;
}

.fund__container-mb {
    display: none;
}

@media screen and (max-width: 900px) {
    .APY__container {
        display: flex;
        list-style: none;
        padding-left: 0;
        max-width: 100%;
        overflow-x: auto;
    }

    .APY__container-fluid {
        display: block;
        /* width: 800px; */
        /* margin-right: 1rem; */
        margin: 0 auto;
    }
}

@media screen and (min-width: 2000px) {
    .portfolio-funding__container {
        flex-wrap: wrap;
        justify-content: flex-end;
        width: 100%;
    }
}

@media screen and (max-width: 1920px) {
    .homebody__header {
        width: 100%;
        padding: 0 1rem;
    }

    .portfolio-funding__container {
        flex-direction: column;
        align-items: flex-end;
        width: 90%;
    }

    .portfolio {
        width: 100%;
    }
    .wrapper__assets-notify {
        flex-wrap: wrap;
    }

    .fund__container {
        margin: 2rem 0;
    }

    .graph__path img {
        width: 100%;
    }
}

@media screen and (max-width: 1200px) {
    .portfolio-funding__container {
        flex-direction: column;
        align-items: flex-end;
    }

    .wrapper__assets-notify {
        flex-direction: column;
        align-items: flex-end;
    }
}

@media screen and (max-width: 650px) {
    .homebody {
        margin-left: 0;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }

    .homebody__head-logo {
        display: block;
        width: 40px;
        height: 40px;
    }

    .homebody__header {
        justify-content: space-between;
    }
    .homebody__header-title {
        display: none;
    }
    .APY__container {
        display: flex;
        list-style: none;
        padding-left: 0;
        max-width: 100%;
        overflow-x: auto;
    }

    .APY__container-fluid {
        display: block;
        width: 300px;
    }
    .portfolio-funding__container {
        width: unset;
        margin: 33px auto 20.5px auto;
    }

    .btn__fund-vaults_mb {
        display: block;
    }

    .btn__fund-mb,
    .btn__vaults-mb {
        width: 152px;
        height: 44px;
        border-radius: 12px;
        font-family: 'Brinnan';
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;

        background: linear-gradient(180deg, #292a2d 0%, #1c1c1c 100%);
        color: #ffffff;
    }

    .btn__fund-mb:hover,
    .btn__vaults-mb:hover {
        background: #000000;
        border: 2px solid rgb(44, 149, 194);
    }

    .btn__fund-mb {
        margin-right: 24px;
    }

    .wrapper__assets-notify {
        margin-bottom: 3rem;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .assets__container {
        width: 100%;
    }

    .assets__content {
        display: none;
    }

    .assets__content-mb {
        display: flex;
        flex-direction: column;
    }

    .homebody__list {
        justify-content: flex-end;
    }

    .homebody__list-item_ethereum-text {
        display: none;
    }

    .homebody__list-item_ethereum {
        width: 35px;
    }
    .homebody__list-item_notify {
        margin: 0 2rem;
    }

    .notify__container {
        margin: 3rem 0;
    }

    .fund__container {
        display: none;
    }

    .fund__container-mb {
        display: block;
    }
}

.App {
    max-width: 3083px;
    margin: 0 auto;
}

.home {
    display: flex;
}

